/*
 * Element: Link Social
 */
.social-link {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #acacac;
  border-radius: 50%;
}
.social-link:hover,
.social-link:focus {
  background: #939393;
}
.social-link__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  fill: #ffffff;
}
.social-link--facebook {
  background-color: #3b5998;
}
.social-link--facebook:hover,
.social-link--facebook:focus {
  background: #2d4373;
}
.social-link--twitter {
  background-color: #00aced;
}
.social-link--twitter:hover,
.social-link--twitter:focus {
  background: #0087ba;
}
.social-link--google-plus {
  background-color: #d34836;
}
.social-link--google-plus:hover,
.social-link--google-plus:focus {
  background: #b03626;
}
/*
 * Element: logo
 */
.logo {
  width: 100px;
}
/*
* Element: Label
*/
.label {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}
.label--block {
  display: block;
}
/*
* Element: Label
*/
.textarea {
  display: block;
  width: 100%;
  min-height: 100px;
  padding: 10px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  resize: none;
}
.textarea:focus {
  outline: none;
}
/*
* Element: Input fields
*/
.input {
  display: block;
  width: 100%;
  height: 40px;
  padding: 0 20px;
  font-size: 16px;
  line-height: 1;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  color: #363636;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  vertical-align: bottom;
}
.input:focus {
  outline: 0;
}
/* Modules
********************/
/*
 * Modul: Social Media Collection
 */
.social-media__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.social-media__item {
  float: left;
  margin-right: 10px;
}
.grid {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 992px) {
  .grid {
    padding-left: 60px;
    padding-right: 60px;
  }
}
.grid--full-width {
  max-width: 2000px;
  padding-left: 0;
  padding-right: 0;
}
.grid--site-width {
  max-width: 1560px;
}
.grid--two-columns {
  max-width: 1500px;
}
.grid--two-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-columns > .grid-column {
    width: 50%;
  }
}
.grid--three-columns {
  max-width: 1500px;
}
.grid--three-columns > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--three-columns > .grid-column {
    width: 33.33%;
  }
}
.grid--two-third {
  max-width: 1500px;
}
.grid--two-third > .grid-column {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column {
    width: 66.66%;
  }
}
.grid--two-third > .grid-column:last-child {
  width: 100%;
}
@media (min-width: 992px) {
  .grid--two-third > .grid-column:last-child {
    width: 33.33%;
  }
}
.form-field + .form-field {
  margin-top: 20px;
}
.link-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.link-list__item + .link-list__item {
  margin-top: 5px;
}
